<!-- eslint-disable vue/no-deprecated-v-bind-sync -->
<template>
  <main class="content container">
  <div class="content__top content__top--catalog">
    <h1 class="content__title">
      Каталог
    </h1>
    <span class="content__info">
      152 товара
    </span>
  </div>

  <div class="content__catalog">

    <ProductFilter v-bind:price-from.sync="filterPriceFrom" v-bind:price-to.sync="filterPriceTo" v-bind:category-id.sync="filterCategoryId" v-bind:color-id.sync="filterColorId"></ProductFilter>

    <section class="catalog" style="position: relative;">

      <div v-if="productsLoading === true">Загрузка товаров...
        <img src="../../img/preloader.gif" style="position: absolute;" alt="Прелоадер">
      </div>
      <div v-if="productsLoadingFail === true">Произошла ошибка при загрузке <button @click.prevent="loadProducts">Попробовать еще раз</button></div>

      <ProductList :products="products"/>

      <BasePagination v-model="page" :count= 'countProducts' :per-page='productsPerPage'></BasePagination>

    </section>

  </div>
</main>

</template>

<script>

import axios from 'axios';
import ProductList from '../components/ProductList.vue';
import BasePagination from '../components/BasePagination.vue';
import ProductFilter from '../components/ProductFilter.vue';
import API_BASE_URL from '../config';

export default {
  components: { ProductList, BasePagination, ProductFilter },
  data() {
    return {
      filterPriceFrom: 0,
      filterPriceTo: 0,
      filterCategoryId: 0,
      filterColorId: 0,

      page: 1,
      productsPerPage: 3,

      productsDate: null,

      productsLoading: false,
      productsLoadingFail: true,
    };
  },
  computed: {
    products() {
      return this.productsDate
        ? this.productsDate.items.map((product) => ({
          ...product,
          image: product.image.file.url,
        }))
        : [];
    },
    countProducts() {
      return this.productsDate ? this.productsDate.pagination.total : 0;
    },
  },
  methods: {
    loadProducts() {
      this.productsLoading = true;
      this.productsLoadingFail = false;
      axios.get(`${API_BASE_URL}/api/products`, {
        params: {
          page: this.page,
          limit: this.productsPerPage,
          categoryId: this.filterCategoryId,
          colorId: this.filterColorId,
          minPrice: this.filterPriceFrom,
          maxPrice: this.filterPriceTo,
        },
      })
        .then((response) => { this.productsDate = response.data; })
        .catch(() => { this.productsLoadingFail = true; })
        .then(() => { this.productsLoading = false; });
    },
  },
  watch: {
    page() {
      this.loadProducts();
    },
    filterPriceFrom() {
      this.loadProducts();
    },
    filterPriceTo() {
      this.loadProducts();
    },
    filterCategoryId() {
      this.loadProducts();
    },
    filterColorId() {
      this.loadProducts();
    },
  },
  created() {
    this.loadProducts();
  },
};
</script>
